/**  
 *   
 * import cache from '@/common/js/cache'  
 *    
 *       
 *         string    cache.put('k', 'string');  
 *         json      cache.put('k', { "b": "3" }, 2);  
 *         array     cache.put('k', [1, 2, 3]);  
 *         boolean   cache.put('k', true);  
 *       
 *             cache.get('k')  
 *         string    cache.get('k', '')  
 *         json      cache.get('k', { "a": "1" })  
 *     /    
 *         : cache.remove('k');  
 *         cache.clear();   
 *   
 * @type {String}  
 */  
const postfix = '_apffapp'; //    
/**  
 *    
 * @param  {[type]} k []  key
 * @param  {[type]} v []  value
 * @param  {[type]} t []  time
 */  
function putSync(k, v, t) {  
    uni.setStorageSync(k, v)   
    let seconds = parseInt(t);  
    if (seconds > 0) {  
        let timestamp = Date.parse(new Date());  
        timestamp = timestamp / 1000 + seconds;  
        uni.setStorageSync(k + postfix, timestamp + "")  
    } else {  
        uni.removeStorageSync(k + postfix)  
    }  
}  

/**  
 *    
 * @param  {[type]} k   []  
 * @param  {[type]} def []  
 */  
function getSync(k, def) {  
    let deadtime = parseInt(uni.getStorageSync(k + postfix))   
    if (deadtime) {  
        if (parseInt(deadtime) < Date.parse(new Date()) / 1000) {  
            if (def) {  
                return def;  
            } else {  
                return false;  
            }  
        }  
    }  
    let res = uni.getStorageSync(k);  
    if (res) {  
        return res;  
    } else {  
        if (def == undefined  || def == "") {  
            def = false;   
        }  
        return def;  
    }  
}  

function getInfoSync(){
	const res = uni.getStorageInfoSync();
}



function removeSync(k) {  
    uni.removeStorageSync(k);  
    uni.removeStorageSync(k + postfix);  
}  

function clearStorageSync(){
	uni.clearStorageSync();
}

/*  success	Function		
	fail	Function		
	complete	Function		 */
/**  
 *     
 * @param  {[type]} k []  
 * @param  {[type]} v []  
 * @param  {[type]} t []  
 */  
function put(k, v, t) {  
    uni.setStorage(k,v);
    let seconds = parseInt(t);  
    if (seconds > 0) {  
        let timestamp = Date.parse(new Date());  //timestamp 
        timestamp = timestamp / 1000 + seconds;  
        uni.setStorage(k + postfix,timestamp + "");  
    } else {  
        uni.removeStorage(k + postfix)  
    }  
}  

/**  
 *    
 * @param  {[type]} k   []  
 * @param  {[type]} def []  
 */  
function get(k, def) {  
    let deadtime = parseInt(uni.getStorage(k + postfix))   
    if (deadtime) {  
        if (parseInt(deadtime) < Date.parse(new Date()) / 1000) {  
            if (def) {  
                return def;  
            } else {  
                return false;  
            }  
        }  
    }  
    let res = uni.getStorage(k);  
    if (res) {  
        return res;  
    } else {  
        if (def == undefined  || def == "") {  
            def = false;   
        }  
        return def;  
    }  
}  

function getInfo(){
	const res = uni.getStorageInfo();
}


function remove(k) {  
    uni.removeStorage(k);  
    uni.removeStorage(k + postfix);  
}  

function clearStorage(){
	uni.clearStorage();
}

export default {
	put,
	get, 
	getInfo,
	remove, 
	clearStorage,
	putSync,
	getSync, 
	getInfoSync,
	removeSync,
	clearStorageSync
}
