import {request} from '@/common/js/request'
export default {
    state: {
        forceLogin:false,//
        hasLogin:false,//
		userName:"",
        userId:"",
        userInfo: {},
        token:""
		
    },
    getters: {
		/* getUserAllInfo(state){
			return {
				forceLogin:state.forceLogin,
				hasLogin:state.hasLogin,
				userName:state.userName,
				userId:state.userId,
				userInfo:state.userInfo
			};
		} */
    },
    mutations: {
		//state
		// store.commit  mutation  payload,param  mutation 
		setStateAttr(state, param){
			if(param instanceof Array){//
				for(let item of param){
					state[item.key] = item.val;
				}
			}else{
				state[param.key] = param.val;
			}
		},
		checkTokenAndLogin(state,user){
			state.hasLogin = true;
			state.userId = user._id;
			state.userInfo = user;
		},
		login(state,user){
			state.userName=user.mobile || '';
			state.hasLogin = true;
			state.userId = user._id;
			state.userInfo = user;
			//state.token = user.token || '';
			/* uni.setStorage({// App.vue
			    key: 'userInfo',  
			    data: provider  
			}) */
		},
		register(state,user,uid){
			state.userName=user.mobile || '';
			state.hasLogin = true;
			state.userId = uid;
			state.userInfo = user;
			//state.token = user.token || '';
			/* uni.setStorage({// App.vue
			    key: 'userInfo',  
			    data: provider  
			}) */
		},
		logout(state){
			state.userName = "";
		    state.hasLogin = false;
		    state.userId = '';
		    state.token = '';
			state.userInfo='';
			
			/* uni.removeStorage({
			    key: 'userInfo'  
			}) */
			uni.removeStorageSync('uni_id_token');
			uni.removeStorageSync('uni_id_token_expired');
		},
		//token
		setToken(state, data){
			const {token, tokenExpired} = data;
			state.token = token;
			uni.setStorageSync('uni_id_token', token);
			uni.setStorageSync('uni_id_token_expired', tokenExpired);
			
			/* this.dispatch('getUserInfo'); //
			this.dispatch('getCartCount');//
			uni.$emit('refreshCart');//
			this.dispatch('getOrderCount'); // */
		}
    },
    actions: { 
		//
		async updateUserInfo({state, commit}){
				commit('setStateAttr', {
					key: 'userInfo',
					val: userInfo
				})
			console.log(res);
		},
		//state
		async getUserInfo({state, commit}){
			const res = await request('userCenter', 'getUserInfo', {
				uid:state.userId
			}, {});
			if(res.result.code === 0){
				const userInfo = res.result.userInfo;
				console.log("state"+userInfo);
				commit('setStateAttr', {
					key: 'userInfo',
					val: userInfo
				});
			}
			console.log(res);
		}
    }
}